.profile__title {
  color: #FFFFFF;
  margin: 0;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  text-overflow: ellipsis;
  max-width: 294px;
  overflow: hidden;
  white-space: nowrap;
}

@media (max-width:640px) {
  .profile__title {
    font-size: 27px;
    line-height: 33px;
    max-width: 189px;

  }
}
